.content-wrapper {
    width: 80%;
    margin: 0 auto;
}

.header {
    padding-top: 3em;
    margin: 0;
    height: 10vh;
    margin: 0 auto;
    text-align: center;

    background: linear-gradient(38deg, #20bc94, #757eba, #804598);
    background-size: 600% 600%;
    -webkit-animation: gradient-rotate 29s ease infinite;
    animation: gradient-rotate 29s ease infinite 
}

@-webkit-keyframes gradient-rotate {
    0%{background-position:0% 51%}
    50%{background-position:100% 50%}
    100%{background-position:0% 51%}
}

@keyframes gradient-rotate {
    0%{background-position:0% 51%}
    50%{background-position:100% 50%}
    100%{background-position:0% 51%}
}

.footer {
    color: #fff;
    font-family: 'Raleway', sans-serif;
    padding: 1em 0;
    margin: 0;
    height: auto;
    margin: 6em auto 0 auto;
    text-align: center;

    background: linear-gradient(38deg, #20bc94, #757eba, #804598);
    background-size: 600% 600%;
    -webkit-animation: gradient-rotate 29s ease infinite;
    animation: gradient-rotate 29s ease infinite 
}

@keyframes gradient-rotate {
    0%{background-position:0% 51%}
    50%{background-position:100% 50%}
    100%{background-position:0% 51%}
}
    

.footer > p > a {
    color: rgb(255, 166, 0);  
    text-decoration: none;
    transition-duration: 1s;
}

.footer > p > a:hover {
    color: #fff;    
}

/* ------------- */

/* Navigation styles */

.nav ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
.nav li {
    display: inline-block;
    margin-right: 1em;
    font-family: 'Oswald', sans-serif;
    font-size: 1.5em;
    line-height: 40px;
    height: 40px;
}

.nav a {
    text-decoration: none;
    padding: 0 1em;
    color: rgb(0, 0, 0);
    display: block;
    transition: 0.5s;
}

.nav a:hover {
    background-color: #fff;
    color: #000;
}

.nav a:active { 
    background-color: #000;
    color: #fff;
}


/* ------------------------------ */

/* Styles for headings */

.title {
    color: #000;
    font-family: 'Open Sans', sans-serif;
    font-size: 5rem;
    font-weight: 100;
    text-align: center;
    padding: 1.2em 0;
}

.sub-title {
    color: #000;
    font-family: 'Raleway', sans-serif;
    font-size: 2rem;
    font-weight: 100;
    text-align: center;
    padding: 1.2em 0; 
}

.sub-title > a {
    color: rgba(32,124,229,1);
    transition-duration: 1s;
}

.sub-title > a:hover {
    color: rgb(255, 166, 0);
}

.landing-title {
    color: #000;
    font-family: 'Open Sans', sans-serif;
    font-size: 6rem;
    margin-bottom: 0.1em;
    font-weight: 100;
    text-align: center;
  }
  
.landing-desc {
    font-family: 'Acme', sans-serif;
    font-weight: 200;
    margin-top: 1em;
    margin-bottom: 3em;
    text-align: center;
  }

/* -------------- */

/* Scroll button */

.scroll-btn {
    width: 18%;
    font-size: 2.5em;
    display: block;
    background: linear-gradient(#ffd000, #ff9854);
    border-radius: 0px;
    text-shadow: 2px 2px 7px #666666;
    box-shadow: 1px 1px 3px #666666;
    font-family: 'Oswald', sans-serif;
    color: #ffffff;
    padding: 0.2em 0.4em;
    cursor: pointer;
    text-align: center;
    margin: 0 auto 10em auto;
    text-decoration: inherit;
  }

  .scroll-btn:hover {
    background: linear-gradient(#ffe68a, #fca874);
  }

  object > p {
      font-family: 'Open Sans', sans-serif;
  }

  object > p > a {
    color: rgba(32,124,229,1);
  }

  object > p > a:hover {
    color: rgb(255, 166, 0);
  }

  /* -------------- */

/* ------------------- */

/* Back to top button */

#topBtn {
    display: none; /*Remains hidden until the user scrolls down*/
    cursor: pointer;
    font-family: 'Alegreya Sans SC', sans-serif;
    font-size: 1.5em;
    position: fixed;
    bottom: 3.5em;
    right: 2em;
    z-index: 2; /* Always on top of other elements */
    border: none;
    outline: none;
    background-color: rgba(0, 0, 0, 0.4);
    color: white; 
    padding: 0.5em;
    transition-duration: 1s;
}

#topBtn:hover {
    background-color: rgba(0, 0, 0, 0.7);
}

s {
    border: solid #fff;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
}

.up {
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg);
}

/* ---------------------- */

/* Contact section styles */
.contact-title {
    color: #000;
    font-family: 'Open Sans', sans-serif;
    font-size: 5rem;
    font-weight: 100;
    text-align: center;
    padding: 1.2em 0 0.5em 0;
}

.contact-box {
    background: rgba(0, 0, 0, 0.6);
    width: 40%;
    height: auto;
    padding: 1em;
    margin: 0 auto 15em auto;
}

.contact-box > p {
    color: #fff;
    text-align: center;
    font-family: 'Raleway', sans-serif;
    font-size: 1.5em;
}

.contact-box > p > a {
    color: rgb(255, 166, 0);
    transition-duration: 1s;
    margin-left: .5em;
}

.contact-box > p > a:hover {
    color: #fff;
}

/* ------------------------- */

/* About section styles */
.about-title {
    color: #000;
    font-family: 'Open Sans', sans-serif;
    font-size: 5rem;
    font-weight: 100;
    text-align: center;
    padding: 0.6em 0;
}

.about-text {
    font-family: 'Raleway', sans-serif;
    font-size: 1.2em;
    color: #000;
    text-align: center;
    font-weight: bold;
    padding: 0 0 2em 0;
}

.skillset{
    margin: auto auto;
    width: 60%;
    padding: 2em;
    background: rgba(0, 0, 0, 0.075);
    vertical-align: center;
}
.skillset ul {
    text-align: left;
    font-family: 'Raleway', sans-serif;
}

.skillset ul > li{
    padding: 0.4em 0;
}

/* ---------------------- */

/*Creations page project list*/

.project{
    width: 100%;
    padding: 1em 0;
}

.project-title{
    color: #000;
    font-family: 'Open Sans', sans-serif;
    font-size: 2em;
    font-weight: 100;
    text-align: center;
    padding: 0.5em auto;
}

.project-body{
    font-family: 'Raleway', sans-serif;
    font-size: 1.2em;
    color: #000; 
}

.project-body > p {
    width: 60%;
    margin: 1em auto 3em auto;
}

.project-body > a {
    text-align: center;
    margin: 0 auto;
    padding: 0 auto;
    text-decoration: none;
}

.project > img{
    width: 100%;
    height: auto;
    margin: 0 auto;
    align-self: center;
}

.project-footer{
    color: #000;
    font-family: 'Open Sans', sans-serif;
    font-size: 1em;
    font-weight: 100;
    text-align: center;
    padding: 0.6em 0 2em 0;
    border-bottom: 1px solid #000;
}

.project-btn {
    width: 18%;
    font-size: 1.5em;
    display: block;
    background: linear-gradient(#739fff, #588dff);
    border-radius: 0px;
    text-shadow: 2px 2px 7px #666666;
    box-shadow: 1px 1px 3px #666666;
    font-family: 'Oswald', sans-serif;
    color: #ffffff;
    padding: 0.2em 0.4em;
    cursor: pointer;
    margin: 0 auto;
  }

  .project-btn:hover {
    background: linear-gradient(#a0beff, #6c9aff);
  }

/* ----------------------------- */

/* Media queries */

@media (max-width: 1100px) {

    .title {
        font-size: 3rem;
    }

    #topBtn {
        font-size: 1.2rem;
        position: fixed;
        bottom: 3.5em;
        right: 1em;
    }

    .scroll-btn {
        width: 40%;
        font-size: 2rem;
    }
    
    .contact-box {
        width: 70%;
    }

    .contact-box > p {
        font-size: 1rem;
    }

    .contact-title {
        font-size: 3rem;
    }

    .project-btn {
        font-size: 1.5em;
        width: 30%;
      }

    .project-body > p {
        width: 70%;
        margin: 1em auto 3em auto;
    }

    .about-text {
        font-size: 1.1em;
        padding: 0 1em 4em 1em;
    }

    .landing-title {
        font-size: 3rem;
      }
  
    .landing-desc {
        font-size: 1rem;
    }

}

@media (max-width: 768px) {

    .nav li {
        padding: 0.2em 0;
        font-size: 1.2em;
    }

    .title {
        font-size: 2rem;
        padding: 1em;
    }

    .sub-title {
        font-size: 1.5rem;
    }

    .header {
        padding-top: 2em;
    }

    #topBtn {
        font-size: 1rem;
        position: fixed;
        bottom: 4em;
        right: 1em;
    }

    .scroll-btn {
        width: 40%;
        font-size: 2rem;
    }
    
    .contact-box {
        width: 70%;
    }

    .contact-box > p {
        font-size: 1rem;
    }

    .contact-title {
        font-size: 2rem;
    }

    .footer {
        font-size: 1rem;
    }

    .scroll-btn {
        font-size: 1.5em;
    }

    .project-btn {
        font-size: 1em;
        width: 40%;
      }

    .project-body > p {
        width: 80%;
        margin: 1em auto 3em auto;
    }

    .skillset{
        width: 80%;
        padding: 1.5em;
    }
    
    .about-text {
        font-size: 1em;
        padding: 0 3em 4em 3em;
        text-align: left;
    }

    .landing-title {
        font-size: 2rem;
      }
    
    .landing-desc {
        font-size: 0.8rem;
    }

}
